import { Box,  Center, Text} from "@chakra-ui/react";
import {Link as Routerlink} from 'react-router-dom'

function Navbar(){



    return (
        <>
       <Center>
       <Box display={"flex"} gap={"50px"} width={"100%"} justifyContent={"space-around"}  bgColor={"#9575CD"} >
            <Box>
                <Text color={"#E1BEE7"} _hover={{color:'#4A148C'}}>KumkumDutta</Text>
                </Box>
            <Box display={"flex"} flexDirection={"row"} gap={"50px"}>
                <Routerlink to="/">
                <Text color={"#E1BEE7"} _hover={{color:'#4A148C'}}>Home</Text>
                </Routerlink>

           
                <Routerlink to="/about">
                <Text color={"#E1BEE7"} _hover={{color:'#4A148C'}}>About</Text>
                </Routerlink>
    <Text color={"#E1BEE7"} _hover={{color:'#4A148C'}}>Skills</Text>
    <Text color={"#E1BEE7"}_hover={{color:'#4A148C'}}>Projects</Text>
    <Text color={"#E1BEE7"} _hover={{color:'#4A148C'}}>Contact</Text>
    <Text color={"#E1BEE7"} _hover={{color:'#4A148C'}}>Resume</Text>
            </Box>
            </Box>
       </Center>
        </>
    





    )
}
export default Navbar